@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.coinContainer{
    display: flex;
    justify-content: center;
    font-family: 'Montserrat', sans-serif;
}
.coinRow{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 100%;
}
.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;

}
.coin h1{
    font-size: 16px;
    width: 150px;
}

.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}
.coinSymbol{
    text-transform: uppercase;
}
.coinData{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%
}
.coinPrice{
    width: 200px;
    text-align: left;
}
.coinVolume{
    width: 350px; 

}
.coinMarketCap{
    width: 155px;
}
.coinPercent{
    width: 100%;

}
.red{
    color: #f00606;
}
.green{
    color: #089708
}

.coin-app {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    font-family: 'Montserrat', sans-serif;
  }
  
  .coin-search {
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
  }
  
  .coin-text {
    margin-bottom: 32px;
    text-align: center;
  }
  
  .coin-input {
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: none;
    background-image: linear-gradient(
      -225deg,
      #ac32e4 0%,
      #7918f2 48%,
      #4801ff 100%
    );
  
    color: #e2e2e2;
  }
  
  .coin-input::placeholder {
    color: #e2e2e2;
  }